import React, { useState } from "react";

function Footer() {
  const [currentTime, setCurrentTime] = useState();
  setInterval(() => {
    var time = new Date().toLocaleTimeString();
    setCurrentTime(time);
  }, 1000);
  return (
    <footer>
      <small>{currentTime}</small>
    </footer>
  );
}

export default Footer;
